<template>
  <div class="barbers">
    <app-table
      title="Barbeiros"
      :currentPage="currentPage"
      :headers="headers"
      :items="itemsModel"
      :loading="loading"
      :qtdPages="qtdPages"
      @onActionClicked="callAction"
      @onChangeOrder="changeOrder"
      @onChangeStatus="changeStatus"
      @onPageChanged="changePage"
      @onSearch="searchValue"
    />
  </div>
</template>

<script>
import AppTable from '@/components/moderators/Table';
import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';

export default {
  name: 'Barbers',
  components: { AppTable },
  mixins: [ DateMixin, SnackbarMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '#', value: 'id', align: 'center', width: '60' },
      { text: 'Imagem', value: 'barberImage', align: 'center', width: '80' },
      { text: 'Nome', value: 'name', align: 'start' },
      { text: 'E-mail', value: 'email', align: 'start' },
      { text: 'Cadastro', value: 'data', align: 'start' },
      { text: 'Status', value: 'enabled', align: 'start' },
      { text: 'Aoes', value: 'actions', align: 'center', width: '60' },
    ],
    items: [],
    loading: true,
    order: null,
    qtdPages: 1,
    search: null,
    status: null
  }),
  computed: {
    itemsModel () {
      return this.items.map((barber) => {
        const action = [
          {
            icon: barber.enabled ? 'lock_open' : 'lock',
            class: barber.enabled ? 'blue' : 'gray3',
            action: barber.enabled ? 'block' : 'unblock',
            text: barber.enabled ? 'Bloquear' : 'Desbloquear',
          },
        ];
        return {
          id: barber.id,
          name: barber.name,
          email: barber.email,
          barberImage: barber.image_url,
          enabled: barber.enabled,
          data: this.formatDateTimeToStringPtBr(new Date(barber.created_at)),
          actions: action,
        };
      });
    }
  },
  mounted() {
    this.getBarbers();
  },
  methods: {
    blockBarber(barber) {
      const errorMessage = 'No foi possvel bloquear o barbeiro!'
      this.$axios.put(`${this.$url_api}/barber/${barber.id}/block`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(b => Number(barber.id) === Number(b.id))
            if (index > -1) {
              let item = this.items[index]
              item.enabled = false
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    },
    callAction(action, item) {
      switch (action) {
        case 'block':
          this.blockBarber(item);
          break;
        case 'unblock':
          this.unblockBarber(item);
          break;
      }
    },
    changePage(page) {
      this.currentPage = page;
      this.getBarbers();
    },
    changeOrder (order) {
      this.order = order
      this.getBarbers()
    },
    changeStatus (status) {
      this.status = status
      this.getBarbers()
    },
    getBarbers() {
      this.loading = true;
      let url = `${this.$url_api}/barber`;
      let operator = '?';
      if (this.search) {
        url += `${operator}search=${this.search}`;
        operator = '&';
      }
      if (this.order) {
        url += `${operator}order=${this.order}`;
        operator = '&';
      }
      if (this.status) url += `${operator}status=${this.status}`;
      
      if (this.currentPage) url += `${operator}page=${this.currentPage}`;
      this.$axios(url)
        .then((response) => {
          this.loading = false;
          if (response.status === 200) {
            if (response.data.codigo === 200) {
              this.currentPage = response.data.corpo.current_page;
              this.qtdPages = response.data.corpo.last_page;
              this.items = response.data.corpo.data
            } else this.setError(response.data.corpo);
          } else this.setError('No foi possvel buscar os barbeiros!');
        })
        .catch(() => this.setError('No foi possvel buscar os barbeiros!'));
    },
    searchValue(val) {
      this.currentPage = null;
      this.search = val;
      this.getBarbers();
    },
    setError(message) {
      this.loading = false;
      this.showSnackbarErro(message);
    },
    unblockBarber(barber) {
      const errorMessage = 'No foi possvel desbloquear o barbeiro!'
      this.$axios.put(`${this.$url_api}/barber/${barber.id}/unblock`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const index = this.items.findIndex(b => barber.id === b.id)
            if (index > -1) {
              let item = this.items[index]
              item.enabled = true
              this.items.splice(index, 1, item)
            }
            this.showSnackbar(response.data.corpo)
          } else this.setErro(errorMessage)
        } else this.setErro(errorMessage)
      }).catch(() => this.setErro(errorMessage))
    },
  },
};
</script>
